import { Link } from 'react-router-dom';
import { BlueButton, WhiteButton } from 'styles/buttonStyles';
import { ImagesSource } from 'components/Images';
import { CLOUD_NAME } from 'utils/GlobalUtils';
import {
  ContentContainer,
  Number,
  Picture,
  StyledContainer,
  Text,
  Title
} from './PageContent.styled';
import { useWindowWidth } from 'hooks/useWindowWidth';
import { imgSizes } from 'utils/commonUtils';
import { HomeSection } from 'styles/globalComponents.styled';

export const PageContent = ({
  id,
  page,
  title,
  description,
  styles,
  linkTo,
  showBookForm
}) => {
  const sectionId = title.toLowerCase().replace(' ', '-');

  const windowWidth = useWindowWidth();

  return (
    <HomeSection styles={styles}>
      <StyledContainer styles={styles}>
        <ContentContainer styles={styles}>
          <Title page={page} styles={styles}>
            {title}
          </Title>
          {page === 'coworking' && windowWidth > 1023 && (
            <Number styles={styles} id={id}>
              {id}
            </Number>
          )}
          <Text styles={styles}>
            {page === 'coworking' && windowWidth < 1024 && (
              <Number styles={styles} id={id}>
                {id}
              </Number>
            )}

            {description}
          </Text>
          {windowWidth < 1024 && (
            <Picture styles={styles}>
              <ImagesSource
                imageName={sectionId}
                page={page}
                sizes={imgSizes}
                type="jpg"
              />
              <img
                width={358}
                height={238}
                src={`${CLOUD_NAME}${page}/jpeg/${page}-${sectionId}.jpg`}
                alt={title}
              />
            </Picture>
          )}
          {linkTo && (
            <>
              {styles !== 'dark' ? (
                <BlueButton as={Link} to={linkTo} style={{ zIndex: 2 }}>
                  Details
                </BlueButton>
              ) : (
                <WhiteButton as={Link} to={linkTo}>
                  Details
                </WhiteButton>
              )}
            </>
          )}
          {!linkTo && (
            <>
              {styles !== 'dark' ? (
                <BlueButton onClick={() => showBookForm(sectionId)}>
                  Book now
                </BlueButton>
              ) : (
                <WhiteButton onClick={() => showBookForm(sectionId)}>
                  Book now
                </WhiteButton>
              )}
            </>
          )}
        </ContentContainer>
        {windowWidth > 1023 && (
          <Picture styles={styles}>
            <ImagesSource
              imageName={sectionId}
              page={page}
              sizes={imgSizes}
              type="jpg"
            />
            <img
              width={windowWidth > 1024 ? 730 : 532}
              height={windowWidth > 1024 ? 714 : 520}
              src={`${CLOUD_NAME}${page}/jpeg/${page}-${sectionId}.jpg`}
              alt={title}
            />
          </Picture>
        )}
      </StyledContainer>
    </HomeSection>
  );
};
